.nav {
  display: flex;
  padding: 8px 15px;

  flex-direction: column;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: flex-start;

  background-color: #f8f8f8;
  border-bottom-color: #e7e7e7;
  border-bottom-width: 1px;
  border-bottom-style: solid;
}

@media (min-width: 768px) {
  .nav {
    flex-direction: row;
  }
}

.nav-brand {
  color: #777;
  text-decoration: none;
  font-size: 18px;
}

.nav-menu {
  display: flex;
  flex-direction: row;

  width: 100%;
}

@media (min-width: 768px) {
  .nav-menu {
    padding-left: 15px;
  }
}

.nav-left {
}

.nav-right {
  margin-left: auto;
}

.nav-items {
}

.nav-item {
  color: #777;
  text-decoration: none;

  padding: 8px;
}

.nav-item:first-child {
  padding-left: 0;
}

.nav-item:last-child {
  padding-right: 0;
}

.nav-item-active {
  font-weight: bold;
}

.main {
  padding: 15px;
}
